<template>
  <vs-dialog v-model="againActive">
    <template #header>
      <h4 class="not-margin">
        <slot name="title">
          {{title}}
        </slot>
      </h4>
    </template>
    
    <div style="height: 60px; display: flex;justify-content: right">
      <vs-button style="width: 80px" transparent @click="submit">
        
      </vs-button>
      <vs-button dark transparent @click="cancel">
        
      </vs-button>
    </div>
  </vs-dialog>
</template>


<script>
// <AgainSubmit title="" :active="active" @cancelActive="active=false"  @submit="delWeeklyReport"></AgainSubmit>
export default {
  name: "AgainSubmit",
  data() {
    return {
      againActive: false,
    }
  },
  watch: {
    //true againActivetruefalse 
    active(e) {
      this.againActive = e;
    },
    againActive(e) {
      //
      if (e === false) {
        this.$emit("cancelActive")
      }
    }
  },
  computed: {
    childActive() {
      return this.againActive
    }
  },
  //title active true/false   submit  
  props: ['title', 'active'],

  methods: {
    //active
    // againActive false    propsactivefalse
    cancel() {
      this.againActive = false;
      this.$emit("cancelActive")
    },
    submit() {
      this.$emit("submit")
    }
  }

}
</script>


<style lang="less" scoped>

</style>