<template>
  <div class="center">
    <vs-table v-model="selected" striped>
      <template #header>
        <!--        <vs-input v-model="search" border placeholder=" ..."/>-->
        <div style="display: flex;">

          <vs-button
              :active="active === false"
              style="margin: 0 20px 0 0;height: 40px;width: 80px"
              @click="isActive"
          >
            
          </vs-button>
          <!--  vs-selectv-if ifbug-->
          <vs-select

              v-if="topicNames!==''"
              v-model="pageAndSelectTopic.selectTopic"
              label-placeholder=""
              state="primary"
              style="margin-top: 10px"
          >
            <vs-option label="" value="0">
              
            </vs-option>
            <vs-option label="" :value="-1">
              
            </vs-option>
            <vs-option-group>
              <div slot="title" style="color:#c9c9cc">
                
              </div>
              <vs-option v-for="e of  topicNames" :key="e.topicId" :label="e.topicName" :value="e.topicId">
                {{ e.topicName }}
              </vs-option>
            </vs-option-group>
          </vs-select>
        </div>

      </template>
      <template #notFound>
        <!--        -->
        <el-empty :image-size="200" description=""></el-empty>
      </template>
      <template #thead>

        <vs-tr>
          <vs-th style="max-width: 100px">
            
          </vs-th>
          <vs-th>
            
          </vs-th>
          <vs-th>
            
          </vs-th>
          <vs-th style="max-width: 220px">
            
          </vs-th>
          <vs-th sort @click="users = $vs.sortData($event, users, 'sex')">
            
          </vs-th>
          <vs-th sort @click="users = $vs.sortData($event, users, 'classId')">
            
          </vs-th>
          <vs-th>
            
          </vs-th>
          <vs-th @click="users = $vs.sortData($event, users, 'studentId')">
            
          </vs-th>
        </vs-tr>
      </template>
      <template #tbody>

        <vs-tr
            v-for="(tr, i) in $vs.getSearch(users, search)"
            :key="i"
            :data="tr"
            :is-selected="selected === tr"

        >
          <vs-td>
            {{ ++i }}
          </vs-td>
          <vs-td>
            {{ tr.name }}
          </vs-td>
          <vs-td>
            {{ tr.age }}
          </vs-td>
          <vs-td>
            {{ tr.email }}
          </vs-td>
          <vs-td>
            {{ tr.sex }}
          </vs-td>
          <vs-td>
            {{ tr.classId }}
          </vs-td>
          <vs-td>
            <div> <span v-if="tr.topic != null" style="color: #999999"> <b style="color: #000000">{{
                tr.topic.topicName
              }}</b> </span>
              <b v-else style="color:#FF4757"></b></div>

          </vs-td>

          <vs-td>
            <vs-button danger style="height: 40px" @click="outClass(tr)">
              
            </vs-button>
          </vs-td>

          <!--          <template #expand>-->
          <!--            <div style="display: flex;width: 100%">-->
          <!--              <vs-avatar>-->
          <!--                <img :src="tr.userPhoto" alt="">-->
          <!--              </vs-avatar>-->
          <!--              <p style="margin-left:20px">  {{ tr.name }}</p>-->
          <!--            </div>-->
          <!--          </template>-->

        </vs-tr>


      </template>
      <template v-if="users.length!==0&&pages!==1" #footer>
        <vs-pagination v-model="pageAndSelectTopic.page" :length="pages" progress/>
      </template>
    </vs-table>

    <!--  -->
    <vs-dialog v-model="active" blur>
      <template #header>
        <h4></h4>
      </template>
      <div class="add-student-center">
        <vs-input
            v-model="queryId"
            :loading="isLoading"
            :state="addState"
            label-placeholder=""
        >
          <template v-if="isNull" #message-danger>
            
          </template>
        </vs-input>
        <vs-button style="margin: 20px" @click="getStudent ">
          
        </vs-button>
        <vs-button :disabled="isDisabled" flat style="margin: 20px" @click="addStudent">
          
        </vs-button>
        <vs-dialog v-model="addActive">
          <div style="display: flex;flex-direction: column;align-items: center">
            <vs-select

                v-model="addStudentTopicId"
                label-placeholder=""
                state="primary"
                style="height: 60px;margin-top: 30px"
            >
              <template v-if="addStudentTopicMsg" #message-danger>
                *
              </template>
              <vs-option v-for="e of  topicNames" :key="e.topicId" :label="e.topicName" :value="e.topicId">
                {{ e.topicName }}
              </vs-option>

            </vs-select>

            <div style="display: flex">
              <vs-button style="margin: 10px;width: 80px;height: 40px" transparent @click="addStudentAxios">
                
              </vs-button>
              <vs-button dark style="margin: 10px;width: 80px;height: 40px" transparent @click="addActive=false">
                
              </vs-button>
            </div>

          </div>

        </vs-dialog>

      </div>

      <template #footer>
        <div style="text-align: center">
          <h4></h4>
          <h4 v-if="queryUser===''"></h4>
          <div v-else class="a">
            <vs-table>
              <template #thead>
                <vs-tr style="justify-content: center">
                  <vs-th> </vs-th>
                  <vs-th> </vs-th>
                  <vs-th> </vs-th>
                </vs-tr>
              </template>
              <template #tbody>
                <vs-tr>
                  <vs-td> {{ queryUser.studentId }}</vs-td>
                  <vs-td> {{ queryUser.name }}</vs-td>
                  <vs-td> {{ queryUser.classId }}</vs-td>
                </vs-tr>
              </template>
            </vs-table>
          </div>
        </div>
      </template>
    </vs-dialog>
    <!--    -->
    <AgainSubmit :active="active2"

                 @cancelActive="active2=false"
                 @submit="outClassAxios"
    >
      <template #title> <b style="color: #FF4757">{{ selected.name }}</b> 
      </template>
    </AgainSubmit>

  </div>
</template>
<script>
import axios from "axios";
import {endLoading, startLoading, startSuccessNotification} from "@/axios/loading";
import AgainSubmit from "@/components/AgainSubmit.vue";


export default {
  components: {AgainSubmit},
  data: () => ({
    active2: false,
    active: false,
    //
    pageAndSelectTopic: {
      page: 1,   //
      selectTopic: '0'  //0
    },
    pages: 1,//
    isShowPage: true,
    search: "",
    selected: '',
    addState: "success",
    states: ["success", "danger"],
    queryId: "",
    addValue: "",
    isLoading: false,
    //,[]
    users: [],
    queryUser: '',
    isNull: false,
    isDisabled: true,
    delLoading: false,
    selectTopic: '',
    // ,
    topicNames: '',
    addActive: false,
    addStudentTopicId: '',
    //
    addStudentTopicMsg: false,
  }),


  watch: {
    pageAndSelectTopic: {
      immediate: true,
      deep: true,
      handler() {

        this.getStudentAllByPage()
      }

    },
    queryId(newVal) {
      if (newVal.trim() !== '') {
        this.isNull = false;
        this.addState = this.states[0]
      }
    },
  },
  created() {
    axios.get("/api/topic/names")
        .then(resp => {

          if (resp.data.code === 200) {
            this.topicNames = resp.data.data;
          }
        })
        .catch(error => {

        })
  },
  methods: {
    //
    getStudentAllByPage() {
      startLoading()
      axios.get("/api/student/page/" + this.pageAndSelectTopic.page + "/topic/" + this.pageAndSelectTopic.selectTopic)
          .then((resp) => {
            if (resp.data.code === 200) {
              this.isShowPage = true;
              //
              this.users = resp.data.data.records;
              //
              this.pageAndSelectTopic.page = resp.data.data.current;
              //
              this.pages = resp.data.data.pages;
            } else {
              this.users = ''

            }

          }).catch((error) => {
        this.isShowPage = false;
        console.log(error);
      }).finally(() => {
        endLoading()
      })
    },
    //
    getStudent() {

      if (this.queryId.trim() === '') {
        this.isNull = true;
        this.addState = this.states[1]
        return
      }
      this.isLoading = !this.isLoading
      axios.get("/api/student/" + this.queryId).then(resp => {
        if (resp.data.code === 200) {
          this.queryUser = resp.data.data
          this.addValue = resp.data.data.studentId;
          this.isDisabled = false
        } else {
          this.addValue = ''
          this.queryUser = ''
          this.isDisabled = true
        }
        this.queryId = ''

        this.isLoading = !this.isLoading
      }).catch(error => {
        console.log(error)
        this.isLoading = !this.isLoading
      })
    },
    //
    isActive() {
      this.addActive = false;
      this.active = true;
      this.isDisabled = true
      this.addStudentTopicMsg = false
      this.addStudentTopicId = ''
      this.addValue = ''
      this.queryUser = ' ' // 
    },
    addStudent() {

      this.addActive = true;

    },
    //
    addStudentAxios() {
      if (this.addStudentTopicId === '') {
        this.addStudentTopicMsg = true;
        return;
      } else this.addStudentTopicMsg = false;

      startLoading()
      axios.post("/api/student", {
        studentId: this.addValue,
        topicId: this.addStudentTopicId
      })
          .then(resp => {
            if (resp.data.code === 200) {
              startSuccessNotification("");
            } else startSuccessNotification(resp.data.msg, '', 1)

          }).catch(error => {
        console.log(error)
      }).finally(() => {
        endLoading()
      })
      this.isDisabled = true
    },
    //
    outClass(tr) {
      if (tr.topic === undefined) {
        startSuccessNotification("", "", 1)
        return
      }
      this.active2 = true
      this.selected = tr

    },
    outClassAxios() {

      let id = this.selected.studentId;
      this.delLoading = true;
      startLoading()
      axios.delete("/api/student/" + id).then(resp => {
        if (resp.data.code === 200) {
          startSuccessNotification("");
          setTimeout(() => {
            history.go(0);
          }, 300)

        } else
          startSuccessNotification("", resp.data.msg, 2)
      }).catch(error => {
        startSuccessNotification("", "", 2)

      }).finally(() => {
          endLoading()
        this.delLoading = false;
      })

    }
  }
};
</script>
<style lang="less" scoped>

.add-student-center {
  width: 500px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}


</style>

        