<template>
  <div class="center">
    <!--    -->
    <div v-if="show">
      <vs-table v-model="selected" striped>
        <template #header>

          <vs-select
              v-model="selectTopic.type"
              label=""
              state="primary"
              style="margin:15px 0 0 0; "
          >
            <vs-option label="" value="publish">
              
            </vs-option>
            <vs-option label="" value="teach">
              
            </vs-option>
          </vs-select>

        </template>
        <template #thead>
          <vs-tr>
            <vs-th style="max-width: 220px">
              
            </vs-th>
            <vs-th>
              
            </vs-th>
            <vs-th>
              
            </vs-th>
            <vs-th>
              
            </vs-th>
            <vs-th>
              
            </vs-th>
            <vs-th>
              
            </vs-th>
            <vs-th>
              
            </vs-th>
            <vs-th>
              
            </vs-th>
          </vs-tr>
        </template>
        <template #tbody>
          <vs-tr
              v-for="(tr, i) in topics"
              :key="i"
              :data="tr"
              :is-selected="selected === tr"
              style="height: 57px"
          >
            <vs-td>
              {{ i + 1 }}
            </vs-td>
            <vs-td>

              <b style="color: #000000">{{ tr.topic.topicName }}</b>

            </vs-td>
            <vs-td>
              {{ tr.topic.topicCredit }}
            </vs-td>
            <vs-td>
              {{ tr.topic.topicPeriod }}
            </vs-td>
            <vs-td>
              {{ tr.topic.gradeRatio }}
            </vs-td>

            <vs-td>
              {{ tr.topic.topicMaxPeople }}
            </vs-td>
            <vs-td style="color:red">
              {{ tr.topic.memberNum }}
            </vs-td>
            <vs-td>

              <span v-for=" (em,i) in tr.teacherNames"> {{ em }}</span>
            </vs-td>
            <template #expand>
              <div style="display: flex;justify-content: space-between">
                <span style="font-size: 13px; min-width: 60px; max-width: 500px;"> <b></b>
                  {{ tr.topic.topicIntroduce }}
                </span>
                <div
                    style="font-size: 12px;color:#999999;text-align: right;display: flex;flex-direction: column;justify-content: center;width: 200px; ">
                  <span><b>{{ tr.topic.createTime }}</b></span>
                  <span><b>{{ tr.topic.updateTime }}</b></span>
                </div>
                <span style="line-height: 42.67px;font-size: 12px;color:#999999">()<b>{{
                    tr.publisher
                  }}</b></span>
                <div class="con-content">
                  <vs-button v-show="selectTopic.type==='publish'" flat icon @click="modifyTopic(tr)">
                    
                  </vs-button>
                  <vs-button v-show="selectTopic.type==='publish'" flat icon @click="addActive=true">
                    
                  </vs-button>
                  <vs-button v-show="selectTopic.type==='publish'" flat icon @click="delActive=true">
                    
                  </vs-button>
                  <vs-button v-show="selectTopic.type==='publish'" border danger @click="againActive=true">
                    
                  </vs-button>

                </div>
              </div>
              <el-divider>
              </el-divider>

            </template>

          </vs-tr>
        </template>
        <template #footer>
          <vs-pagination v-if="length!==1&&length!==0" v-model=" selectTopic.page" :length="length" not-margin/>
        </template>
        <template #notFound>
          <!--        -->
          <el-empty :image-size="200"></el-empty>
        </template>

      </vs-table>
      <!--      -->
      <AgainSubmit :active="againActive"
                   @cancelActive="againActive=false"
                   @submit="deleteTopic(selected)"
      >
        <template #title><h4></h4></template>
      </AgainSubmit>
      <!--      -->
      <vs-dialog v-model="addActive">
        <template #header>
          <!--          if  {{ selected.topic.topicName }} , -->
          <h4 v-if="addActive">
            <b style="color: #195BFF">{{ selected.topic.topicName }}</b>
          </h4>
        </template>

        <div style="display: flex;justify-content: center">

          <vs-input v-model="teacherId" label-placeholder=""
                    type="number">
          </vs-input>

        </div>
        <template #footer>

          <vs-button :loading="isLoading" block @click="inviteAndQuitTeacherSubmit(1)">
            
          </vs-button>


        </template>
      </vs-dialog>

      <!--    -->
      <vs-dialog v-model="delActive">
        <template #header>
          <!--          if  {{ selected.topic.topicName }} , -->
          <h4 v-if="delActive">
            <b style="color: #195BFF">{{ selected.topic.topicName }}</b>
          </h4>
        </template>

        <div style="display: flex;justify-content: center">

          <vs-input v-model="teacherId" label-placeholder=""
                    type="number">
          </vs-input>

        </div>
        <template #footer>

          <vs-button :loading="isLoading" block @click=" inviteAndQuitTeacherSubmit(2)">
            
          </vs-button>


        </template>
      </vs-dialog>
    </div>

    <!--    -->
    <div v-else>
      <el-page-header class="back" content="" title=""
                      @back="show = !show;">
      </el-page-header>
      <add-topic :back="show" :tr="tr" @backEvent="show=!show"></add-topic>
    </div>

  </div>

</template>
<script>
import AgainSubmit from "@/components/AgainSubmit.vue";
import axios from "axios";
import {endLoading, startLoading, startSuccessNotification} from "@/axios/loading";
import AddTopic from "@/pages/AdmPage/addTopic.vue";

export default {
  components: {AgainSubmit, AddTopic},
  data: () => ({
    show: true,
    //
    selected: null,
    //  ,
    selectTopic: {
      type: 'publish',
      page: 1
    },
    //
    page: 1,
    length: 1,
    againActive: false,
    tr: '213',
    //
    topics: '',
    //
    addActive: false,
    //id
    teacherId: '',
    //axios
    isLoading: false,
    delActive: false,
  }),

  watch: {
    selectTopic: {
      immediate: true,
      deep: true,
      handler() {
        startLoading()
        axios.get("/api/topic/" + this.selectTopic.type + "/page/" + this.selectTopic.page)
            .then(resp => {
              if (resp.data.code === 200) {
                //
                this.topics = resp.data.data.records;
                //
                this.length = resp.data.data.pages;
                //
                this.selectTopic.page = resp.data.data.current;
                //
              }
            })
            .catch(error => {
              console.log(error)
            }).finally(() => {
          endLoading()
        })
      }
    },
  },
  methods: {

    //
    modifyTopic(tr) {
      this.show = false;

      //tr
      this.tr = tr
    },
    //
    deleteTopic(tr) {
      let id = tr.topic.topicId;
      this.againActive = false;
      startLoading()
      axios.delete("/api/topic/" + id,)
          .then(resp => {
            if (resp.data.code === 200) {
              startSuccessNotification("");
              history.go(0)
            }
          })
          .catch(error => {
            console.log(error)
          }).finally(() => {
        endLoading()
      })

    },
    //
    inviteAndQuitTeacherSubmit(index) {
      if (this.teacherId === '') {
        startSuccessNotification("", '', 1);
        return;
      }
      //
      if (index === 1) {
        axios.post("/api/teach", {teacherId: this.teacherId, topicId: this.selected.topic.topicId})
            .then(resp => {
              if (resp.data.code === 200) {
                startSuccessNotification("!!!");
                setTimeout(() => {
                  history.go(0)
                }, 500)

              } else startSuccessNotification(resp.data.msg, '', 1)
            })
            .catch(error => {

            })
      }
      //
      if (index === 2) {
        axios.delete("/api/teach/" + this.teacherId + "/topic/" + this.selected.topic.topicId)
            .then(resp => {
              if (resp.data.code === 200) {
                startSuccessNotification("!!!");
                setTimeout(() => {
                  history.go(0)
                }, 500)

              } else startSuccessNotification(resp.data.msg, '', 1)
            })
            .catch(error => {

            })
      }
    }
  }
}
</script>
<style lang="less" scoped>
.back {
  color: #195bff;

  :hover {
    color: #0045f6;
  }
}

.con-content {
  display: flex;
  justify-content: end;
}
</style>

        