<template>
  <div
      class="content">
    <h3 style="text-align: center">{{ title }}</h3>
    <el-divider></el-divider>
    <div class="form">

      <vs-input v-model="form.topicName" border class="form-item" label=""
                placeholder=""
      >

        <template v-if=" (form.topicName.trim().length>10||form.topicName.trim().length<3)&&form.topicName!==''"
                  #message-warn>
           3~10
        </template>
        <template v-else #message-danger>
          *
        </template>
      </vs-input>
      <vs-select
          v-model="form.topicCredit"
          class="form-item"
          color="success"
          label=""
          placeholder=""
          style="max-width: 300px"
      >

        <vs-option label="1.0" value="1">
          1
        </vs-option>
        <vs-option label="2.0" value="2">
          2
        </vs-option>
        <vs-option label="3.0" value="3">
          3
        </vs-option>
        <vs-option label="4.0" value="4">
          4
        </vs-option>
        <vs-option label="5.0" value="5">
          5
        </vs-option>

      </vs-select>

      <el-input v-model="form.topicIntroduce"

                class="form-item"
                maxlength="100"
                placeholder=""
                resize="none"
                rows="6"
                show-word-limit
                type="textarea"
      ></el-input>


      <vs-input
          v-model="form.topicPeriod"
          border
          class="form-item"
          label=""
          max="30"
          min="1"
          placeholder="1-30"
          type="number"
      >
        <template v-if=" (form.topicPeriod>30||form.topicPeriod<1)&&form.topicMaxPeople!==''" #message-danger>
          1~30
        </template>
      </vs-input>
      <vs-input
          v-model="form.topicMaxPeople"
          border
          class="form-item"
          label=""
          max="15"
          min="10"
          placeholder="30"
          type="number"

      >
        <template v-if=" (form.topicMaxPeople>15||form.topicMaxPeople<10)&&form.topicMaxPeople!==''" #message-danger>
          10~15
        </template>

      </vs-input>
      <el-divider></el-divider>
      <vs-select
          v-model="form.gradeRatio"
          class="form-item"
          label=""
          placeholder="/"
          state="primary"
          style="max-width: 300px"
      >
        <vs-option label="/=0.4" value="0.4">
          0.4
        </vs-option>
        <vs-option label="/=0.5" value="0.5">
          0.5
        </vs-option>
        <vs-option label="/=0.6" value="0.6">
          0.6
        </vs-option>


      </vs-select>
      <vs-button @click="againActive=true;">
        {{ title }}
      </vs-button>
      <vs-dialog v-model="againActive">
        <template #header>
          <h4 class="not-margin">
            
          </h4>
        </template>
        <div style="height: 60px; display: flex;justify-content: right">
          <vs-button style="width: 80px" transparent @click="selectAddOrModify">
            
          </vs-button>
          <vs-button dark transparent @click="againActive=false">
            
          </vs-button>
        </div>
      </vs-dialog>
    </div>

  </div>
</template>

<script>


import {endLoading, startLoading, startSuccessNotification} from "@/axios/loading";
import axios from "axios";

export default {
  data() {
    return {
      title: '',
      //
      againActive: false,
      form: {
        //
        topicName: '',
        //
        topicIntroduce: '',
        //
        topicCredit: '',
        //
        topicPeriod: 15,
        //
        memberNum: 0,
        //
        topicMaxPeople: 15,
        //
        gradeRatio: 0.5,
        //
        createTime: '',
        //
        updateTime: ''
      },
      //true
      formSuccess: {
        topicName: false,
        topicIntroduce: false,
        topicCredit: false,
        topicPeriod: true,
        topicMaxPeople: true,
        gradeRatio: true,
      }
    }
  },
  props: ['tr', 'back'],
  watch: {
    tr: {
      immediate: true,
      deep: true,
      handler(tr) {
        this.initModifyTopic(tr);
      }
    }
  },
  methods: {
    // 
    selectAddOrModify() {
      //iftr 
      if (this.tr === '' || this.tr === undefined) {
        this.submit();
        return;
      }
      //
      this.modifyTopic();
    },
    //
    requireForm() {
      this.formSuccess.topicName = !(this.form.topicName.trim().length > 10 || this.form.topicName.trim().length < 3);
      this.formSuccess.topicIntroduce = !(this.form.topicIntroduce.length <= 0);
      this.formSuccess.topicCredit = !(this.form.topicCredit === '')
      this.formSuccess.topicPeriod = !(this.form.topicPeriod > 30 || this.form.topicPeriod < 1)
      this.formSuccess.topicMaxPeople = !(this.form.topicMaxPeople > 15 || this.form.topicMaxPeople < 10)
      this.formSuccess.gradeRatio = !(this.form.gradeRatio === '')
      //count==6
      let count = 0;
      for (let i in this.formSuccess) {
        if (this.formSuccess[i] === true) {
          count++;
        }
      }
      return count;
    },
    //
    submit() {
      if (this.requireForm() !== 6) {
        startSuccessNotification("", "", 2)
        return;
      }

      axios.post("/api/topic", this.form,)
          .then(resp => {
            if (resp.data.code === 200) {
              startSuccessNotification("");

            } else if (resp.data.code === 1005) startSuccessNotification("", "", 1)
          })
          .catch(error => {
          })
          .finally(() => {
            endLoading()
          })
      //
      this.againActive = false;
    },
    //tr 
    initModifyTopic(tr) {
      // 
      if (tr === '' || tr === undefined) return;
      //,
      tr.topic.createTime = null;
      tr.topic.updateTime = null;
      this.form = tr.topic;
      this.title = ''
    },
    //
    modifyTopic() {
      startLoading()
      axios.put("/api/topic", this.form )
          .then(resp => {
            if (resp.data.code === 200) {
              startSuccessNotification("");


            } else {
              startSuccessNotification("", "", 1)
            }
          })
          .catch(error => {
          })
          .finally(() => {
              endLoading()
            this.$emit('backEvent')
          })
      this.againActive = false;
    }

  }
};
</script>
<style lang="less" scoped>
.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;

}

.form {
  box-sizing: border-box;
  width: 900px;
  height: 700px;
  padding: 15px 50px;

  //border: 1px solid red;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;

  .form-item {
    margin: 20px 0;
    width: 300px !important;
    max-width: 300px !important;
  }
}

.vs-input {
  width: 300px !important;
  max-width: 300px !important;
}
</style>